import { Dialog, Col, Row, Button, Form, FormItem, Input } from "element-ui"

export default {
    components: {
        'el-dialog': Dialog,
        'el-col': Col,
        'el-row': Row,
        'el-button': Button,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-input': Input
    },
    props: ['show'],
    data () {
        return {
            dialogShow: this.show,
            form: {
                account: '',
                nickname: ''
            }
        }
    },
    methods: {
        close: function() {
            this.$emit('close');
        },
        add: function() {
            axios.post('api/wechat/service/add', this.form).then(response => {
                console.log(response.data);
            });
        }
    },
    watch: {
        show (val) {
            this.dialogShow = val;
        },
        dialogShow (val) {
            if (!val) {
                this.close();
            }
        },
    }
}



